<template>
    <main>
        <button v-b-toggle.collapse-settings class="settings">Settings</button>

        <b-collapse  id="collapse-settings" class=" collapse-wrapper">
            <div class="d-flex wrapper-items">
                <input v-model="transportValue"   class="form-control mx-2" type="text">
                <b-button @click="transportId = transportValue" variant="success" size="sm">Задати № транспорту</b-button>
            </div>
        </b-collapse>

        <div :class="{'text-disabled': !statusCard}" class="card m-2">
            <div class="info d-flex ">
                <div class="img-wrapper">
                    <img :src='headerImg'>
                </div>
                <div class="info-text">
                    <div>
                        Львів
                    </div>
                    <div>
                        ЛЬВІВЕЛЕКТРОТРАНС
                    </div>
                    <div>
                        <span class="episode">
                            Серія
                        </span>
                        <span>
                            {{!statusCard ? objCard.episode : episode}}
                        </span>
                    </div>
                </div>
            </div>
            <div class="content mt-3">
                <div class="img-content-wrapper">
                    <img :src='contentImg' alt="">
                </div>
                <div class="transport-id">
                    <span> №  {{!statusCard ? objCard.transportId : transportId}}</span>
                </div>
                <div class="name-trans">
                    Вагон
                </div>
                <div class="date-info d-flex justify-content-between  text-center ">
                    <div class="date">
                        <div class="date__name">
                            Дата
                        </div>
                        <div style="font-weight: 500">
                            {{!statusCard ? objCard.DateNewCard : DateNewCard}}

                        </div>
                    </div>
                    <div class="time">
                        <div class="time__name">
                            Час
                        </div>
                        <div style="font-weight: 500">
                            {{!statusCard ? objCard.timeBought : timeBought}}

                        </div>
                    </div>
                    <div class="type-ticket">
                        <div class="ticket-name">
                            Стандартний
                        </div>
                        <div style="font-weight: 500">
                            {{count}} шт.
                        </div>
                    </div>
                </div>
                <div :class="{'text-disabled': !statusCard}" class="text-ticker-info ">
                    Квиток разового використання
                </div>
                <div v-if="statusCard"  class="validation">
                    <button v-if="dateStart === '' ||  statusCard === false " @click="dateTimer(1,true)"  class="btn-validation">
                        Закомпостувати
                    </button>
                    <div class="timer-date text-center">
                        {{date}}
                    </div>

                </div>
            </div>
        </div>
    </main>
</template>


<script>


    export default {
        name: "CardOpen",
        props:{
            statusCard : {
                type: Boolean,
                default: true,
            },
            objCard:Object,
        },
        data(){
            return{

                transportValue:'',
                transportId: null,
                count: 1,
                DateNewCard: '',
                timeBought: '',
                date:'',
                setInt: Function,
                dateStart: '',
                episode:'',
                arrEpisod:['246287618','672331946','996147219','186892964','998554794']
            }
        },


        computed:{
            headerImg(){
                return this.statusCard ? require('../assets/img/headerImg.6238dea2.jpg') : require('../assets/img/headerImgDisable.b33a08dc.jpg')
            },
            contentImg(){
                return this.statusCard ? require('../assets/img/contentImg.4322b141.jpg') : require('../assets/img/contentImgDisable.0987833c.jpg')
            },
            randomEpisod(){
                return Math.floor(Math.random() * this.arrEpisod.length);
            },

            dateToDay(){
                let date = new Date();
                let day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
                let month = +date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}`: date.getMonth() +1;
                let year = date.getFullYear();
                return  `${day}.${month}.${year}`;

            },

         

        
        },
        beforeMount(){

            if(localStorage.getItem('date')){
                const storageDate = JSON.parse( localStorage.getItem('date'))
                this.dateStart = storageDate.dateStart;
                this.DateNewCard = storageDate.DateNewCard;
                this.timeBought = storageDate.timeBought;
                this.transportId = storageDate.transportId;
                this.episode = storageDate.episode;
                this.dateTimer();
            }
            if( !localStorage.getItem('closedCard')){
                localStorage.setItem('closedCard',JSON.stringify([]))
            }

        },

        methods:{

            addToArrClosedCard(){

                if(localStorage.getItem('closedCard')){
                    const objClosedCard = {transportId: this.transportId,
                        timeBought: this.timeBought,
                        DateNewCard:this.DateNewCard,
                        episode:this.episode
                    };
                    const cardsClosed =  JSON.parse( localStorage.getItem('closedCard'));
                    cardsClosed.push(objClosedCard)
                    localStorage.setItem('closedCard',JSON.stringify(cardsClosed))
                }
            },
            timer(){
                let distance = this.dateStart - new Date().getTime();
                if(distance <= 0) {
                    localStorage.removeItem('date');
                    this.addToArrClosedCard();
                    this.$emit('closedCard')
                    clearInterval(this.setInt);
                    this.DateNewCard= '',
                        this.timeBought='',
                        this.dateStart = '';
                    this.date = '';
                    return;
                }
                let minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
                let seconds = Math.floor((distance % (1000 * 60)) / 1000);
                this.date = ` ${minutes < 10 ?  '0' + minutes : minutes} : ${seconds < 10 ? '0' + seconds: seconds}`
            },
            dateTimer(hour = 0, startTicker =  false ) {

                if(startTicker === true) {
                    const date = new Date();
                    this.dateStart = new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate() , new Date().getHours() + hour   ,new Date().getMinutes() ,new Date().getSeconds() ).getTime();
                    this.DateNewCard = this.dateToDay;
                    this.timeBought =   `${date.getHours() < 10 ?  '0 ' + date.getHours(): date.getHours() }:${date.getMinutes()  < 10 ? '0' + date.getMinutes() : date.getMinutes() }:${date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()}`;
                    this.episode = this.arrEpisod[this.randomEpisod]
                }

                window.onbeforeunload = ()=>{
                    if(this.dateStart !== '') localStorage.setItem('date',JSON.stringify({dateStart: this.dateStart,
                        timeBought: this.timeBought,
                        DateNewCard: this.DateNewCard,
                        transportId: this.transportId,
                        episode: this.episode }));
                };

                this.timer();
                this.setInt = setInterval(this.timer,1000)
            }

        }
    }
</script>

<style >
    :root{
        --color-text:#8A8A8A;
    }
    .img-wrapper img{
        height: 70px;
        width: 70px;
    }
    .card{
        padding: 12px 15px 3px 15px;
        box-shadow: 0px 1px 2px 1px rgba(0,0,0,0.39);
        -webkit-box-shadow: 0px 1px 2px 1px rgba(0,0,0,0.39);
        -moz-box-shadow: 0px 1px 2px 1px rgba(0,0,0,0.39);
    }
    .img-wrapper{
        height: 100%;
        box-shadow: 0px 1px 2px 1px rgba(0,0,0,0.39);
        -webkit-box-shadow: 0px 1px 2px 1px rgba(0,0,0,0.39);
        -moz-box-shadow: 0px 1px 2px 1px rgba(0,0,0,0.39);
    }
    .info-text{
        margin-left: 8px;
        font-weight: 500;

    }
    .episode{
        color: var(--color-text);
    }
    .img-content-wrapper{
        text-align: center;
    }
    .img-content-wrapper img{
        max-width: 130px;
        height: 130px;
        width: 100%;

    }
    .transport-id{
        margin-top: 10px;
        text-align: center;
        font-weight: 500;
        font-size: 30px;
    }
    .date-info{
        line-height: normal;
        margin:  10px 0 0 0;
    }
    .date{
        flex: 0 0 100px;
    }
    .time{
        flex: 0 0 100px;
    }
    .type-ticket{
        flex: 0 0 100px;
    }
    .name-trans{
        text-align: center;
        color: var(--color-text);
    }
    .date__name{
        color: var(--color-text);
    }
    .time__name{
        color: var(--color-text);
    }
    .ticket-name{
        color: var(--color-text);
    }
    .text-ticker-info{
        margin-top: 5px;
        margin-bottom: 5px;
        text-align: center;
        color: #9ac242;
        font-weight: 500;
        font-size: 14px;
    }
    .text-disabled{
        color: var(--color-text);
    }
    .validation{
        margin-top: 10px;
    }

    .btn-validation{
        cursor: pointer;
        border-radius: 10px;
        border: 0;
        background: #8DC641;
        width: 100%;
        font-weight: 400;
        font-size: 17px;
        padding: 7px 0;
        color: #fff;
    }
    .timer-date{
        color: #95BB50;
        font-size: 25px;
        font-weight: 700;
    }
    .settings{
        position: absolute;
        z-index: 100;
        top: 0;
        right: 0;
        opacity: 0;
    }
    .wrapper-items{
        padding: 20px;
    }
    .collapse-wrapper{
        z-index: 200;
        width: 100%;
        background: rgba(0, 0, 0, 0.46);
        position: absolute;
        top: 4%;
    }

</style>