<template>
    <div id = "app">
        <Header/>
        
        <CardOpen @closedCard = "closedCard"/>
        
        <CardOpen :key = "index" :objCard = "list" :statusCard = 'false' v-for = "(list,index) in filteredClosedCards"/>
        <div v-if = "arrClosedCard.length > sliceCount && arrClosedCard.length > filteredClosedCards.length" class = "more-cards">
            <button @click = "showMoreCards"><i class = "fas fa-chevron-down mx-1"></i>Ще</button>
        </div>
    
    </div>
</template>

<script>




    import CardOpen from "./components/CardOpen";
    import Header from "./components/Header";
    export default {
        name: 'App',
        components: {Header, CardOpen, },
        data() {
            return {
                arrClosedCard: [],
                sliceCount: 2
            };
        },
        beforeMount() {
            if(localStorage.getItem('closedCard')) {
                this.arrClosedCard = JSON.parse(localStorage.getItem('closedCard'));
            }

        },

        computed: {
            //filtered cloced Card array
            filteredClosedCards() {
                return this.arrClosedCard.slice(0, this.sliceCount);
            },
        },

        methods: {

            showMoreCards() {
                console.log(this.arrClosedCard.slice(0, this.sliceCount));
                this.sliceCount += 2;
            },
            closedCard() {
                console.log(JSON.parse(localStorage.getItem('closedCard')));
                this.arrClosedCard = JSON.parse(localStorage.getItem('closedCard'));
            },
        }

    };
</script>

<style>
    .more-cards button {
        width: 100%;
        background: transparent;
        border: 0;
        color: #9ac242;
        font-size: 16px;
        margin: 7px 0;
    }
    
    .more-cards:active {
        background: rgba(0, 0, 0, 0.21);
        transition: 0.4s;
    }
</style>
